import router from './router'
import store from './store'
import { Message } from 'element-ui'
import NProgress from 'nprogress' // 进度条
import 'nprogress/nprogress.css' // 进度条样式
import { getToken } from '@/utils/auth' // 从存储中拿cookie
import getPageTitle from '@/utils/get-page-title'
import menuList from '@/assets/js/menuList'
NProgress.configure({ showSpinner: false }) // 进度条配置

const whiteList = ['/login', '/auth-redirect'] // 白名单

router.beforeEach(async(to, from, next) => {
  // 开始进度条
  NProgress.start()

  // 设置title
  document.title = getPageTitle(to.meta.title)

  // 根据token判断用户是否已登录
  const hasToken = getToken()

  if (hasToken) {
    if (to.path === '/login') {
      // 如果转到登录页面则定向到主页
      next({ path: '/' })
      NProgress.done()
    } else {
      // 不是的话判断用户角色
      // 用户角色,决定用户可访问的页面
      // 先去store中找用户信息
      const hasRoles = store.getters.roles && store.getters.roles.length > 0
      if (hasRoles) {
        next()
      } else {
        // 没有的话根据token去后端请求用户信息
        try {
          // 注意: roles 必须是一个对象数组! 比如: ['admin'] or ,['developer','editor']
          const { roles } = await store.dispatch('user/getInfo')
          // 基于角色生成可访问路由表
          const accessRoutes = await store.dispatch('permission/generateRoutes', roles)
          // 动态添加可访问路由
          router.addRoutes(accessRoutes)
          // 修改侧边栏
          menuList.resetMenuList(roles)
          // 跳转到to中保存的原本跳转页面
          // 设置repalace true 让浏览器不留下记录
          next({ ...to, replace: true })
          // next()
        } catch (error) {
          // 消除token并且返回登录页面
          await store.dispatch('user/resetToken')
          Message.error(error || 'Has Error')
          next(`/login?redirect=${to.path}`)
          NProgress.done()
        }
      }
    }
  } else {
    /* 未登录,没有token的情况下 */
    if (whiteList.indexOf(to.path) !== -1) {
      // 预先设定的白名单,存放不需要权限的路由
      next()
    } else {
      // 如果没有token又不在白名单路由
      // 返回登录并存放本来需要跳转的页面
      next(`/login?redirect=${to.path}`)
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  // 进度条结束
  NProgress.done()
})
